@import '../../../../../../../../constants.scss'; 

.schedulerForm {

  &__forms {
    padding: 15px 55px;
  }

  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .title {
      @include primary_title;
    }

    .clear-icon {
      cursor: pointer;
    }
  }

  &__editButton {
    background-color: white!important;
    color: $primary!important;
    border: $border_primary;
  }
}